.App {
	text-align: center;
}

.App-logo {
	height: 40vmin;
	pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
	.App-logo {
		animation: App-logo-spin infinite 20s linear;
	}
}

.App-header {
	background-color: #282c34;
	min-height: 100vh;
	display: flex;
	flex-direction: column;
	align-items: center;
	justify-content: center;
	font-size: calc(10px + 2vmin);
	color: white;
}

.App-link {
	color: #61dafb;
}

@keyframes App-logo-spin {
	from {
		transform: rotate(0deg);
	}
	to {
		transform: rotate(360deg);
	}
}

html {
	scroll-padding-top: 70px; /* height of sticky header */
}

nav {
	border-bottom: 4px solid black;
	text-align: center;
	top: 0;
	width: 100%;
	background: white;
	padding: 5px;
}

.navbar-static-top {
	display: inline-flex;
	justify-content: center;
}

.navbar-static-top ul{
	display: inline-flex;
}

.nav-left {
	position: absolute;
	left: 0px;
}

.nav-right {
	position: absolute;
	right: 50px;	
}

.logout{
	color: #22acbe;
	position: absolute;
	padding: 7px;
	width: 50px;
}

.signup input[type="email"]{
	margin: 2px;
}

.signup input[type="submit"]  {
	width: 125px;
}

.login input[type="text"],input[type="password"] {
	margin: 1px;
	margin-top: 0px;
}

.logout{
	color: #22acbe;
}

.logout:hover {
	color: white;
	background-color: #20b3a6;
	border-radius: 5px;
}

.teal {
	color: white !important;
	background-color: #22acbe !important;
	border-color: #22acbe !important;
	border-radius: 5px;
}

.teal:hover {
	color: white;
	background-color: #20b3a6 !important;
	border-color: #20b3a6 !important;
	border-radius: 5px;
}

ul {
	list-style-type: none;
	margin: 0;
	padding: 0;
}

li {
	display: inline-block;
	padding-left: 9px;
	padding-right: 9px;
}


input[type="submit"]{
	margin-top: 4px;
}

.menu-header {
	background-color: white;
	border: azure;
	color: rgb(27, 146, 216);
}

.menu-spacing {
	text-align: center;
	display: inline-block;
	width: max-content;
	height: max-content;
	padding: 6px;
}

.menu-spacing span {
	color: maroon;
	display: block;
}

.item-form {
	text-align: center;
	background-color: rgb(180, 180, 180);
	display: block;
}

.item-form label {
	margin-bottom: 3px;
}

.item-form input[type='text'],
input[type='number'] {
	display: block;
	margin: auto;
	margin-bottom: 3px;
}

.item-form button {
	margin-top: 2px;
	margin-bottom: 6px;
	width: 120px;
}

.flexbox {
	display: flex;
	flex-wrap: wrap;
}

.latest-single {
	width: 70%;
	background-color: white;
	padding-left: 2px;	
	font-family: 'Nanum Gothic', sans-serif;
}

table a {
	color: lightslategray;
}

.main-header {
	display: inline-flex;
	width: 100%;
	background-color: red;
}

.main-header button {
	background-color: gray;
	color: white;
	position: absolute;
	right: 30%;
}

.sidebar {
	background-color: rgb(65, 64, 64);
	width: 30%;
	text-align: center;
	display: inline-block;
}
.sidebar-opts {
	justify-content: center;
	display: flex;
	width: 100%;
}

.sidebar-opts input[type='text'] {
	margin-bottom: 3px;
}

.wide-btn {
	padding-left: 55px;
	padding-right: 55px;
	padding-top: 2px;
	padding-bottom: 2px;
}

.sidebar h2 {
	color: white;
	margin-top: 4px;
}

.sidebar ul {
	list-style-type: none;
	margin: 0;
	padding: 0;
	width: 100%;
	display: block;
}

.sidebar li {
	display: block;
	background-color: #555;
	color: white;
	padding: 8px 30px;
	text-decoration: none;
}

.sidebar li:hover {
	background-color: orange;
	color: black;
}

.create-list {
	background-color: #20b3a6 !important;
	border-color: #20b3a6!important;
}

.create-list:hover {
	background-color: #26d0c1 !important;
	border-color: #26d0c1 !important;
}

.list-by-code {
	background-color: #252525 !important;
	border-color: #252525 !important;
}

.list-by-code:hover {
	background-color: #353434 !important;
	border-color: #353434 !important;
}


.list-header {
	display: flex;
	margin-top: 2px;
	justify-content: space-between;
}

.members-btn {
	background-color: #424242 !important;
	border-color: #424242 !important;
	margin-left: auto;
	margin-top: 6px;
	margin-right: 2px;
}

.members-btn:hover {
	background-color: #4e4e4e !important;
	border-color: #4e4e4e !important;
}

.orange-btn {
	background-color: rgb(252, 172, 25) !important;
	color: white;
	margin-top: 0px !important;
	width: 70px;
}

.orange-btn-menu {
	background-color: rgb(252, 172, 25) !important;
	color: white;
	margin-top: 0px !important;
}

.orange-btn:hover {
	background-color: rgb(253, 179, 42) !important;
}

.orange-btn:hover span {display:none}
.orange-btn:hover:before {content:"Done?"}

.page-not-found{
    position: absolute; 
	top: 50%;
	text-align: center;
	width: 100%;
	color: grey;
}

.about{
	text-align: center !important;
	font-family: 'Nanum Gothic', sans-serif;
    width: 100vw;
    height: 95vh;
    position: relative;
}
.blurredPhoto {
	position: absolute;
	width: 100vw;
    height: 95vh;
	
	background-attachment: fixed;
	background-position: center;
	background-repeat: no-repeat;
	background-size: cover;
	background-image: url("https://hips.hearstapps.com/hmg-prod.s3.amazonaws.com/images/directly-above-shot-of-fruits-and-vegetables-in-royalty-free-image-910893590-1556652427.jpg");
	-webkit-filter: blur(3px);
}

.about-text{
	position: absolute;	
	background-color: rgba(0,0,0, 0.2); /* Black w/opacity/see-through */
	color: white;
	height: 95vh !important;
	width: 100vw;
	padding-top: 40vh;
	text-align: center;
}

.about-body{
	padding-bottom: 100px;
	text-align: center !important;
}

.about-body h1{
	padding: 40px;
}

.about a{
	color: #35c2d4;
	font-size: 15px;
	margin-top: 30px;
}

.about-body a{
	color: #22acbe;
	font-size: 15px;
	margin-top: 30px;
}

.dark-mode{
	background-color: #252525 !important;
	color: white;
}


@media only screen and (max-width: 720px) {
	.sidebar-opts {
		display: block;
	}
	.latest-single{
		width: 100%;
	}
	.sidebar{
		width: 100%;
	}
	.flexbox{
		height: 100%;
	}
	.about-body img{
		width: 100vw;
	}
	.navbar-static-top {
		display: block;
		height: 7vh;
	}
	.navbar-static-top img{
		position: absolute;
		left: 0px;
	}
	.nav-right {
		position: absolute;
		right: 0px;	
	}
	.logout{
		right: 15px;
	}
}